<template>
  <div id="app">
    <div class="container">
      <div class="image">

      </div>
      <div class="detail">
        <div class="search-box">
          <input type="text" 
          class="search-bar"
           placeholder="Country Name"
           v-model="query"
           @keypress="fetchapi"
           >
        </div>
        <div class="detail-container">
        <div class="name">{{json.country}} &nbsp; <small class="continent">{{json.continent}}</small></div>
        <table>
          <tr>
            <td>Total Active </td>
            <td> : </td>
            <td >{{json.active}}</td>
          </tr>
          <tr>
            <td>Total Cases </td>
            <td> : </td>
            <td >{{json.cases}}</td>
          </tr>
          <tr>
            <td>Total Deaths </td>
            <td> : </td>
            <td >{{json.deaths}}</td>
          </tr>
          <tr>
            <td>Total Recovered </td>
            <td> : </td>
            <td >{{json.recovered}}</td>
          </tr>
          <tr>
            <td>Total Population </td>
            <td> : </td>
            <td >{{json.population}}</td>
          </tr>
        </table>
        </div>
        <div class="date">
          <div class="update">
            As of {{date()}}<br>
            <strong class="last">
            Photos from <a href="https://unsplash.com/" target="_blank">unsplash</a> <br>
            logo from <a href="https://www.freelogodesign.org/" target="_blank">Free logo design</a> <br>
            Data from corona.lmao.ninja 
            </strong>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data(){
    return{
      url_base:"https://corona.lmao.ninja/v2/countries/",
      query:'',
      json:{}
    }
  },
  methods:{
    fetchapi(e){
      if(e.key=="Enter"){
        fetch(`${this.url_base}${this.query}`)
        .then( res=>{
          return res.json();
        })
        .then(this.SetResults)
      }
    },
    SetResults(results){
      this.json=results
    },
    date(){
      var d= new Date();
      let year =d.getFullYear();
      let date =d.getDate();
      let month=d.getMonth();

      return `${year}/${date}/${month}`
    }
  }
}

</script>

